mat-list-item {
  button {
    max-width: 36px !important;
    max-height: 36px !important;
    min-width: 36px !important;
    min-height: 36px !important;
    font-size: 12px !important;
  }
}

.seating-plan {
  max-width: 100vw;
  height: max-content;
  overflow-x: auto;
  overflow-y: clip;
  font-size: 12px;
  div {
    width: max-content;
    height: max-content;
  }
}


::-webkit-scrollbar {
  width: 5em;
}
